/************ Default Css ************/
* {
  margin: 0;
  padding: 0;
  font-family: 'Yanone Kaffeesatz', sans-serif;

  
}

html {
  scroll-behavior: smooth;
  font-family: 'Yanone Kaffeesatz', sans-serif;
}

body {
  font-weight: 400;
  overflow-x: hidden;
  position: relative;
  color: #fff !important;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  background-color: white;
}




@media (min-width:1700px) {
	main .container {
		max-width: 100%;
		padding: 0 150px;
	}
}

p.success {
	color: green;
}

p.danger {
	color: red;
}
/************ Banner Css ************/
.banner {
  margin:auto;
  padding: 160px 20px 230px 20px;
  background-image: url('./assets/images/backgroundBanner.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  box-sizing: content-box;
  
  max-height: 600px;
  
  font-family: 'Yanone Kaffeesatz', sans-serif;
}

@keyframes moving{
  from {
    -moz-transform: scale(1.0);
    transform: scale(1.0);
    -webkit-transform: scale(1.0);
    -ms-transform: scale(1.0);
    -ms-filter: "progid:DXImageTransform.Microsoft.Matrix(M11=1.0, M12=0, M21=0, M22=1.0, SizingMethod='auto expand')";
    filter: progid:DXImageTransform.Microsoft.Matrix(M11=1.0, M12=0, M21=0, M22=1.0, SizingMethod='auto expand');
  }
  to {
    -moz-transform: scale(1.5);
    transform: scale(1.5);
    -webkit-transform: scale(1.5);
    -ms-transform: scale(1.5);
    -ms-filter: "progid:DXImageTransform.Microsoft.Matrix(M11=1.5, M12=0, M21=0, M22=1.5, SizingMethod='auto expand')";
    filter: progid:DXImageTransform.Microsoft.Matrix(M11=1.5, M12=0, M21=0, M22=1.5, SizingMethod='auto expand');
  }
}







.banner .tagline {
  font-weight: 700;
  letter-spacing: 0.8px;
  padding: 3px 10px;  
  font-size: 25px;
  margin-bottom: 1px;
  display: inline-block;
  color:#D4F6CC ;
}
.banner h1 {
  font-size: 36px;
  font-weight: 700;
  letter-spacing: 0.8px;
  line-height: 1;
  margin-bottom: 20px;
  display: block;
  color:#125bc9;
}

.banner h1 .fsb{
  color: #00d2f7;
}

.banner h1 .txt-rotate{
  color: #0cd4ef;
  font-weight: 600;
  font-size: 36px;
}



.banner p {
  color: #B8B8B8;
  font-size: 18px;
  letter-spacing: 0.8px;
  line-height: 1.5em;
  width: 96%;
}
.banner button {
  color: #0cd4ef;
  background-image: linear-gradient(to left, #0cd4ef, #D4F6CC);
  font-weight: 500;
  font-size: 25px;
  align-items: center;
  justify-content: center;
  margin-top: 60px;
  letter-spacing: 0.8px;
  display: flex;
  align-items: center;
  width: 200px;
  height: 50px;
  font-family: 'Yanone Kaffeesatz', sans-serif;
}
.banner button svg {
  font-size: 25px;
  margin-left: 10px;
  transition: 0.3s ease-in-out;
  line-height: 1;
}
.banner button:hover svg {
  margin-left: 25px;
}

.banner button .myresume{
  color: #003865;
  font-weight: 600;
  font-size: 18px;
  text-decoration: none;
}

.banner button .myresume:hover{
  color: green;
  
}







.banner img {
  animation: updown 3s linear infinite;
}
@keyframes updown {
    0% {
        transform: translateY(-20px);
    }
    50% {
        transform: translateY(20px);
    }
    100% {
        transform: translateY(-20px);
    }
}
.txt-rotate > .wrap {
  border-right: 0.08em solid #666;
}







span {
  white-space: nowrap;
}

.heading-text.heading-section h2::before {
  content: unset;
}

.heading-text.heading-line h1::before,
.heading-text.heading-line h2::before,
.heading-text.heading-line h3::before,
.heading-text.heading-line h4::before,
.heading-text.heading-line h5::before,
.heading-text.heading-line h6::before {
  content: unset !important;
}

.step_container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0cd4ef;
  color: white;
  height: 35px;
  width: 35px;
  border-radius: 35px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-weight: 400;
  font-size: medium;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}

.icon-box.border.small .icon {
  height: 22px;
  width: 22px;
}

.icon-box.medium > p {
  margin: 0 12px 10px 36px;
}

.icon-box.medium > h3,
.icon-box.medium > h4,
.icon-box.medium > h5 {
  margin: 0 12px 10px 36px;
}
.icon-box.border:hover > .icon {
  border: 6px solid #0cd4ef;
}

@media only screen and (max-width: 990px) {
  .step_paragraph {
    display: none;
  }

  .step_title {
    overflow-wrap: anywhere;
  }

  .authenticate-btn {
    display: none;
  }
}

@media only screen and (min-width: 990px) {
  .authenticate-btn_active {
    display: none;
  }
}
