/*  import google fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Ubuntu:wght@400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@400;500;600&family=Comforter&family=Kaushan+Script&family=Oxygen&family=Pacifico&family=Syne+Tactile&family=Yellowtail&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Courgette&family=Dancing+Script:wght@700&family=Lobster&family=Lobster+Two:ital,wght@1,700&family=Pacifico&family=Poppins:wght@400;500;600;700&family=Quintessential&family=Ubuntu:wght@500;700&family=Yanone+Kaffeesatz:wght@200;300;400;500;600;700&display=swap');



.banner_home{
    position: relative;
    height:100vh;
    overflow: hidden;
    display: grid;
    grid-column-gap: 0px;
    grid-template-areas: 
    
    "main   main   main   .      sidebar";
   
    color: white;
    font-family: 'Yanone Kaffeesatz', sans-serif;
}

.banner_home::after{
    content: '';
    position: absolute;
    z-index:-1;
    left:0;
    bottom:0;

}
.banner_home::before{
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top:0;
    left:0;
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -ms-backface-visibility:hidden;
    backface-visibility: hidden;

    -webkit-transform: translateZ(0)scale(1.0,1.0);
    -moz-transform:translateZ(0)scale(1.0,1.0);
    -o-transform:translateZ(0)scale(1.0,1.0);
    transform: translateZ(0)scale(1.0,1.0);


    background-image: url('./backgroundBanner.jpg');
    -webkit-background-size:cover;
    background-size:cover;
    background-attachment: fixed;
    animation: increase 10s linear 10ms infinite;
    -webkit-transition: all 0.2s ease-in-out;
    -moz-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    z-index: -2;
}


.banner_home .item-b{
    grid-area: main;
}
.banner_home .item-c{
    grid-area: sidebar;
}










.banner_home_text1{
    
    padding:18% 2% 0 2% ;
    

}

.banner_home_text2{
    
    padding:21% 2% 0 2% ;
    

}

.banner_home_text1 h1{
    text-transform: uppercase;
    font-size: 50px;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.3);
    padding-right: 15px;
    padding-left: 15px;
  

}
.banner_home_text1 .lynxcoder1{
    color: rgb(110, 214, 255);
    font-family: 'Yanone Kaffeesatz', sans-serif;
}

.banner_home_text1 .lynxcoder{
    color: rgb(157, 255, 0);
    font-family: 'Yanone Kaffeesatz', sans-serif;
}

.banner_home_text1 .lynxcoder2{
    color: rgb(0, 183, 255);
    font-family: 'Yanone Kaffeesatz', sans-serif;
}
.banner_home_text1 .lynxcoder3{
    color: rgb(0, 183, 255);
    font-family: 'Yanone Kaffeesatz', sans-serif;
}

.banner_home_text1 .txt-rotate{
    color: rgb(255, 145, 0);
    font-family: 'Yanone Kaffeesatz', sans-serif;
}


.banner_home_text2 img{
    padding: 0% 3% ;
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 20px;
  

}

@keyframes increase{
    0%{
        transform: scale(1);
    }
    100%{
        transform: scale(1.5);
    }
    
}





@media screen and (max-width: 700px) {
    .banner_home{
        
        height:100vh;
        overflow: hidden;
        display: block;
        color: white;
        justify-content: start;
        font-family: 'Yanone Kaffeesatz', sans-serif;
    }
    .banner_home_text1{
        padding-top: 7%;
    }
   

      .banner_home_text2{
       padding-bottom: 20%;
       
      }
    .banner_home_text2 img{
        padding-top: 0px;
        justify-content: start;
        width: 20px;
        border-radius: 20px;
      }
}